<template>
  <div id="posts">
    <div v-for="post in posts">
      <div v-if="category === 'home'">
        <div class="postDivider"></div>

        <div id="snippetHeader">
          <h2 v-html="post.postTitle"></h2>
        </div>

        <div class="postSnippet" v-html="post.postSnippet"></div>

        <div id="buttonStyling">
          <router-link :to="{ name: 'dd-post', params: {  id:post.postID } }">
            <button>read more</button>
          </router-link>
        </div>
      </div>
      <div v-else-if="post.postCategory === category">
        <div class="postDivider"></div>

        <div id="snippetHeader">
          <h2 v-html="post.postTitle"></h2>
        </div>

        <div class="postSnippet" v-html="post.postSnippet"></div>

        <div id="buttonStyling">
          <router-link :to="{ name:'dd-post', params: {  id:post.postID } }">
            <button>read more</button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from "../firebase";
import post from "@/components/Post";

export default {
  data: function() {
    return {
      posts: ""
    };
  },
  props: ["category"],
  firestore: {
    posts: db.collection("posts").orderBy("postID", "desc") //order by category -> select incoming category?
  },
  components: {
    "dd-post": post
  }
};
</script>

<style scoped>
.postSnippet {
  width: 96%;
  padding-left: 6%;
  margin: auto;
  text-align: left;
  margin-bottom: 20px;
}

#snippetHeader {
  background-color: white;
  padding-top: 10px;
  padding-left: 6%;
  margin-bottom: 2%;
  text-align: left;
}

#buttonStyling {
  display: flex;
  clear: both;
  margin-left: 7%;
}

.postSnippet >>> p {
  text-align: left;
}

.postSnippet >>> h2 {
  text-align: left;
  margin-left: 7%;
}

.postSnippet >>> img {
  -webkit-box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5);
  box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5);
  border: 1px solid grey;
  margin-right: 20px;
  margin-bottom: 20px;
}
</style>



