<template>
  <div id="post">
    <div v-for="post in posts">
      <div v-if="post.postID == id">
        <div id="pageIntroduction">
          <h1 v-html="post.postTitle"></h1>
        </div>
        <div class="postDivider"></div>

        <div class="postContent" v-html="post.postContent"></div>
      </div>
      <!-- <Perhaps make a 404 component? -->
    </div>
  </div>
</template>

<script>
import { db } from "../firebase";
export default {
  data: function() {
    return { posts: "" };
  },
  props: ["id"],
  firestore: {
    posts: db.collection("posts").orderBy("postID", "desc") //order by category -> select incoming category?
  }
};
</script>

<style scoped>
.postContent >>> p {
  text-align: left;
  margin-left: 9%;
}

.postContent >>> h2 {
  text-align: left;
  margin-left: 7%;
}

.postContent >>> h3 {
  text-align: left;
  margin-left: 7%;
}

.postContent >>> li {
  text-align: left;
  margin-left: 7%;
}
.postContent >>> ul {
  text-align: left;
  margin-left: 3%;
}

.postContent >>> img {
  -webkit-box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5);
  box-shadow: 3px 3px 3px 1px rgba(0, 0, 0, 0.5);
  border: 1px solid grey;
}
</style>


