<template>
  <div id="games">
    <div id="pageIntroduction">
      <h1>Games</h1>
    </div>
    <div class="postDivider"></div>
    <div id="gamesContent">
      <img src="./../assets/worldAxisLogo.png" />
      <p>
        My current game project is
        <i>World Axis</i>
        , An action-RPG with pixel art graphics and a story based on my DnD campaign.
      </p>
    </div>
    <div id="gamePosts">
      <h1>Updates</h1>
    </div>
    <dd-posttile category="Games"></dd-posttile>
  </div>
</template>

<script>
import PostTile from "@/components/PostTile";

export default {
  data: function() {
    return {};
  },
  components: {
    "dd-posttile": PostTile
  }
};
</script>

<style scoped>
#games {
  padding-top: 100px;
  text-align: left;
  height: 100vh;
}

#gamePosts {
  padding-left: 6%;
  padding-right: 6%;
}

#gamesContent {
  width: 96%;
  padding-left: 6%;
  padding-right: 6%;
  margin: auto;

  margin-bottom: 40px;
  margin-top: 80px;
  display: flex;
}
p {
  margin: auto; /* Important */
}

img {
  width: 300px;
  height: 341px;
}
</style>



