<template>
  <div id="home">
    <div id="pageIntroduction">
      <p>This site is currently under development, sorry!</p>
    </div>
    <about></about>
    <games></games>
    <artwork></artwork>
    <websites></websites>
    <!-- <form>
      <label>
        New Todo:
        <input v-model="newTodo" type="text" />
        <button type="submit" @click.prevent="addTodo()">Add</button>
      </label>
    </form>
    <ul>
      <li v-for="todo in todos" :key="todo.id">
        <p>{{todo.text}}</p>
      </li>
    </ul>-->
  </div>
</template>

<script>
import { db } from "../firebase";
import PostTile from "@/components/PostTile";
import about from "@/views/About";
import games from "@/views/Games";
import artwork from "@/views/Art";
import websites from "@/views/Websites";

export default {
  name: "home",
  data() {
    return {
      newTodo: "",
      todos: []
    };
  },
  firestore: {
    todos: db.collection("todos").orderBy("createdAt", "desc")
  },
  methods: {
    addTodo() {
      db.collection("todos")
        .add({
          text: this.newTodo,
          completed: false,
          id: this.todos.length,
          createdAt: new Date()
        })
        .then(function(docRef) {
          console.log("Document written with ID: ", docRef.id);
        })
        .catch(function(error) {
          console.error("Error adding document: ", error);
        });
      this.newTodo = "";
    }
  },
  components: {
    "dd-posttile": PostTile,
    about: about,
    games: games,
    artwork: artwork,
    websites: websites
  }
};
</script>

<style>
ul {
  list-style-type: none;
}
</style>


